import { Component, Input } from '@angular/core';
import { Http, URLSearchParams, Response } from '@angular/http';
import { window } from '@angular/platform-browser/src/facade/browser';
import { ReplaySubject } from 'rxjs/ReplaySubject'

@Component({
  selector: 'youtube',
  styles: [`
  `],
  template: require('./youtube.component.html')

})
export class YoutubeComponent{
    private player;
    
    @Input()
    videoId:string;
    
    constructor(){}

  ngOnInit() {
    const doc = window.document;
    let playerApiScript: HTMLScriptElement = doc.createElement("script");
    alert("ready");
    window.onYouTubeIframeAPIReady = ()=>{
      alert("complete");
      this.loadComplete()};
       playerApiScript.onload = () => {alert("load complete");};
      //  playerApiScript.state = () => {alert("load complete");};
    // playerApiScript.onload = this.loadComplete;
    playerApiScript.type = "text/javascript";
    playerApiScript.src = "http://www.youtube.com/iframe_api";
    doc.body.appendChild(playerApiScript);
  }

  loadComplete(){
    this.player = new (<any>window).YT.Player('video', {
        //   height: '360',
           width: '100%',
          videoId: this.videoId,
          events: {
            'onReady': this.onPlayerReady,
            'onStateChange': this.onPlayerStateChange
          }
        });
  }

  onPlayerReady(){}
  onPlayerStateChange(){}
}


